.work-section {
  margin-top: 72px;

  @media screen and (min-width: 768px) {
    margin-top: 100px;
  }

  @media screen and (min-width: 1200px) {
    margin-top: 128px;
  }
}

.work-section__titel {
  font-weight: 600;
  font-size: 24px;
  line-height: 28px;
  text-align: center;
  letter-spacing: -0.03em;
  text-transform: uppercase;
  color: black;
  flex: none;
  order: 0;
  flex-grow: 0;
  margin-bottom: 91px;
}

.work-section__span {
  color: #fd9222;
}

.work-section__list {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 0px;
  gap: 28px;
  flex: none;
  order: 0;
  flex-grow: 0;
}

.work-section__item {
  background: #ffffff;
  border: 1px solid rgba(17, 17, 17, 0.1);
  box-shadow: 0px 9.04932px 26.5717px rgba(215, 228, 249, 0.25);
  border-radius: 15px;
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
  max-width: 360px;
  height: 200px;
  padding-top: 56px;
  padding-bottom: 26px;
  padding-left: 24px;
  padding-right: 24px;
  position: relative;
}

.work-section__img {
  position: absolute;
  top: -20%;
}

.work-section__name {
  font-weight: 600;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
  color: #111111;
  margin-bottom: 24px;
}

.work-section__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  letter-spacing: -0.02em;
  color: rgba(17, 17, 17, 0.7);
  max-width: 312px;
}

.work-section__div {
  width: 13px;
  height: 13px;
  background: rgba(17, 17, 17, 0.1);
  border-radius: 50%;
}

.work-section__flex-div {
  display: flex;
  width: 106px;
  margin-left: auto;
  margin-right: auto;
  gap: 13px;
  margin-top: 32px;
}

.active-div {
  width: 28px;
  height: 13px;
  background: #fd9222;
  border-radius: 15px;
}

.work-section__button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  padding: 14px 40px;
  gap: 14px;
  width: 222px;
  height: 50px;
  border: 1px solid #fd9222;
  border-radius: 100px;
  flex: none;
  order: 2;
  flex-grow: 0;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  color: #111111;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
  background-color: transparent;
  transition: background-color 500ms linear 250ms, color 500ms linear 250ms;
}

.work-section__button:hover {
  background-color: #FD9222;
  color: #FFFFFF;
}

// modal

.backdrop {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: opacity 500ms cubic-bezier(0.17, 0.67, 0.83, 0.67) 250ms;
  background-color: rgba(0, 0, 0, 0.2);
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.work-section__modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 311px;
  background-color: #fafafa;
  border-radius: 15px;
  padding-top: 32px;
  padding-left: 24px;
  padding-right: 24px;
  padding-bottom: 32px;

  @media screen and (min-width: 768px) {
    width: 517px;
    padding-top: 40px;
    padding-left: 40px;
    padding-bottom: 40px;
  }
}

.work-section__modal-title {
  font-weight: 600;
  font-size: 20px;
  line-height: 28px;
  letter-spacing: -0.03em;
  text-transform: uppercase;
  color: #111111;
  display: flex;
  flex-direction: column;
  margin-bottom: 40px;

  @media screen and (min-width: 768px) {
    font-size: 24px;
    display: block;
    max-width: 298px;
  }
}

.work-section__modal-span {
  color: #fd9222;
}

.work-section__button-close {
  position: absolute;
  top: 15px;
  right: 15px;
  background-color: transparent;
  border: none;
}

.work-section__close-icon {
  width: 28px;
  height: 28px;
  stroke: #111111;

  z-index: 1000;
}

.work-section__form {
  display: block;
}

.work-section__input {
  display: block;
  align-items: flex-start;
  padding-left: 24px;
  width: 100%;
  height: 66px;
  border-radius: 15px;
  border: 1px solid rgba(17, 17, 17, 0.1);
  background-color: transparent;
  margin-bottom: 19px;
}

.work-section__input:last-child {
  padding-left: 177px;
}

.tel-input {
  padding-left: 106px;

  @media screen and (min-width: 768px) {
    padding-left: 177px;
  }
}

.work-section__tel-div {
  width: 100%;
}

.work-section__tel-div-2 {
  position: absolute;
  width: 74px;
  height: 32px;
  display: flex;
  align-items: center;
  top: 315px;
  left: 42px;
  border-right: #4040404d solid 1px;

  @media screen and (min-width: 768px) {
    left: 64px;
    top: 323px;
    width: 125px;
  }
}

.flag-Ukrain {
  margin-right: 8px;
  width: 24px;
  height: 18px;

  @media screen and (min-width: 768px) {
    margin-right: 18px;
    width: 32px;
    height: 24px;
  }
}



.work-section__label {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  display: flex;
  align-items: center;
  letter-spacing: -0.02em;
  color: #000000;
}

.work-section__input::placeholder {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  display: flex;
  align-items: center;
  letter-spacing: -0.02em;
  color: rgba(64, 64, 64, 0.3);
}

.work-section__textarea {
  display: block;
  border: 1px solid rgba(17, 17, 17, 0.1);
  margin-bottom: 18px;
  padding-left: 24px;
  padding: 22px;
  width: 100%;
  height: 137px;
  border-radius: 15px;
  resize: none;
}

.work-section__textarea::placeholder {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  display: flex;
  align-items: center;
  letter-spacing: -0.02em;
  color: rgba(64, 64, 64, 0.3);
}

.work-section__send-button {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 14px 40px;
  width: 117px;
  height: 45px;
  background-color: #FD9222;
  border-radius: 100px;
  border: none;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: #FFFFFF;
}

.work-section__modal-wraper {
  min-width: 255px;
}

// modal-2

.backdrop-2 {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: opacity 500ms cubic-bezier(0.17, 0.67, 0.83, 0.67) 250ms;
  background-color: rgba(0, 0, 0, 0.2);
}

.is-hidden2 {
  opacity: 0;
  pointer-events: none;
}

.modal-2 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 311px;
  height: 350px;
  background-color: #FD9222;
  border-radius: 15px;
  padding-top: 84px;
  padding-left: 58px;
  overflow: hidden;
  position: relative;

  @media screen and (min-width: 768px) {
    width: 516px;
    height: 414px;
    padding-top: 163px;
    padding-left: 170px;
  }
}

.modal-2__button {
  position: absolute;
  width: 30px;
  height: 30px;
  right: 14px;
  top: 14px;
  background-color: #FCF1E0;
  border: none;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  @media screen and (min-width: 768px) {
    right: 24px;
  }
}

.modal-2__icon {
  width: 22px;
  height: 22px;
  stroke: #F16529;
}

.modal-2__titel {
  font-weight: 600;
  font-size: 20px;
  line-height: 28px;
  letter-spacing: -0.02em;
  text-transform: uppercase;
  color: #FCF1E0;
  margin-bottom: 8px;
  @media screen and (min-width: 768px) {
    font-size: 24px;
  }
}

.modal-2__text {
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: -0.02em;
  color: #FFFFFF;
  @media screen and (min-width: 768px) {
    font-size: 14px;
  }
}

.modal-2__img {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 220px;
  height: 200px;

  @media screen and (min-width:768px) {
    width: 296px;
    height: 330px;
  }
}

.work-item-none {
  @media screen and (min-width: 320px) {
    display: none;
  }

  @media screen and (min-width: 1200px) {
    display: flex;
  }
}

.work-item-none-2 {
  @media screen and (min-width: 768px) {
    display: flex;
  }
}